import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import './style.css';

const Chapters = () => {
  return (
    <div className='chapters-container'>
      <Helmet>
        <title></title>
        <meta name="description" content="List of chapters of my project" />
      </Helmet>
      <h2 className='chapters-title'></h2>
      <div className='chapters-list'>
        <Link to="/chapter/1/page/1" className='chapter-link'></Link>
        <Link to="/chapter/2/page/1" className='chapter-link'> </Link>
        <Link to="/chapter/3/page/1" className='chapter-link'>QR </Link>
        <Link to="/chapter/4/page/1" className='chapter-link'>  </Link>
      </div>
    </div>
  );
}

export default Chapters;