import React from 'react';
import { Link } from 'react-router-dom';
import './style.css'

const Footer = () => {
  return (
    <footer className="footer">
      <div className="footer-content">
        <div className="footer-section">
          <h3> </h3>
          <p>     .</p>
        </div>
        
        <div className="footer-section">
          <h3></h3>
          <ul>
            <li><Link to="/"></Link></li>
            <li><Link to="/chapters"></Link></li>
            <li><Link to="/title"> </Link></li>
          </ul>
        </div>
        
        <div className="footer-section">
          <h3> </h3>
          <ul>
            <li><Link to="/chapter/1/page/1"> 1</Link></li>
            <li><Link to="/chapter/2/page/1"> 2</Link></li>
            <li><Link to="/chapter/3/page/1"> 3</Link></li>
            <li><Link to="/chapter/4/page/1"> 4</Link></li>
          </ul>
        </div>
      </div>
      
      <div className="footer-bottom">
        <p>&copy; 2024  .   .</p>
      </div>
    </footer>
  );
}

export default Footer;